@import url('https://fonts.googleapis.com/css2?family=Lexend:wght@100;200;300;400;500;600;700;800;900&display=swap');

body {
    font-family: 'Lexend', sans-serif;
    font-weight: 300;
    color: #202020;
}

h2 {
    font-weight: 800;
    color: #404040;
}

.notification-card {
    margin: 1.5em 0;
    padding: 1em;
    border-radius: 10px;
    border-width: 3px;
    border-style: solid;
}

.notification-success {
    border-color: green;
    color: green;
    background-color: rgba(0, 255, 0, 0.2);
}

.notification-error {
    border-color: red;
    color: red;
    background-color: rgba(255, 0, 0, 0.2);
}

.notification-title {
    font-size: 1.5rem;
    font-weight: 700;
    margin-bottom: 0.5em;
}

.notification-message {
    color: #303030;
}